
export default (state = { tags:{}}, action) => {
    const newState = Object.assign({}, state);

    if (action.type === 'SET_NAME') {
        const { id, name } = action.file;
        newState.tags = Object.assign({}, newState.tags, {
            [id]: name
        });
    }

    return newState;
};


