import React, { Component } from 'react';
import ListFiles from './drive/ListFiles';
import Search from './search/Search';

export default class SearchFiles extends Component {
    constructor(props) {
        super(props);
        this.state = { searchCriteria: '' };
    }

    render = () => {
        const searchParamsFolders = this.constructCriteria();
        return <div><Search clear={this.clear} getMatches={this.filter} />
            <ListFiles searchParams={searchParamsFolders}>
            </ListFiles></div>;
    }

    clear = () => {
        if (this.state.searchCriteria !== '') {
            this.setState({ searchCriteria: '' });
        }
    }

    filter = (criteria) => {
        if (this.state.searchCriteria !== criteria) {
            this.setState({ searchCriteria: criteria });
        }
    }

    /**
     * null -> all File
     * 'name = "photo-documentation"' -> exact match
     * 'mimeType="application/vnd.google-apps.folder"' -> folders
     */
    constructCriteria = () => {
        if (this.state.searchCriteria) {
            return `name contains "${this.state.searchCriteria}"`;
        }
        return null;
    }
}


